import React, {Component} from 'react'
import axios from 'axios'
import Lottie from 'react-lottie'
import CoinsTopItem from "./layout/parts/coins"
import picture from "./libs/picture"
import { Redirect } from "react-router-dom";


export default class SearchMatch extends Component {
    constructor(props){
        super(props)
        this.state = {user: null, opponent: null, chooseBundle: false, bundle: null, bundleList: null}
        this.userData();
    }


    userData = async () => {
        let params = new URLSearchParams();
        params.append('action', "userData");


        await axios("https://clickadsbr.com/api.php", {method: "post", data: params, withCredentials: true}).then((res)=> {
            if(res.data.logged){
              this.setState({user: res.data});
              console.log(res.data)
            }

            console.log(res.data)
         })
    }
    
    searchOpponent = async ()=> {
        let params = new URLSearchParams();
        params.append('action', "searchMatch");


        await axios("https://clickadsbr.com/api.php", {method: "post", data: params, withCredentials: true}).then((res)=> {
            if(res.data.status){
              this.setState({opponent: res.data});
              console.log(res.data)
              if(this.state.opponent.isOnline){
                  this.setState({OpponentStts: <small style={{
                    background: '#ffffff',
                    width: 'fit-content',
                    padding: '2px 10px',
                    borderRadius: '14px',
                    margin: 'auto',
                    color: '#000'}}> Online </small>});
              }
            }



         })
    }


    bundles = async () => {


        let params = new URLSearchParams();
        params.append('action', "playBundles");


        await axios("https://clickadsbr.com/api.php", {method: "post", data: params, withCredentials: true}).then((res)=> {
          if(res.data.status){
            console.log(res.data)
            this.setState({bundleList: res.data.res})
            this.setState({chooseBundle: true})
          }
            

            //console.log(res.data)
         })
    }



    setBundle = async (e)=> {
        //alert ("bundle " + e);
        
        let params = new URLSearchParams();
        params.append('action', "startBattle");
        params.append('opponent', this.state.opponent.id);
        params.append("bundle", e);
        

        await axios("https://clickadsbr.com/api.php", {method: "post", data: params, withCredentials: true}).then((res)=> {
          if(res.data.status){
            console.log(res.data)
            this.setState({bundle: true});
          }
            
          

            console.log(res.data)
         })



    }


    render(){
        if(this.state.opponent === null){
        return(<>
        <CoinsTopItem user={this.state.user} />
        
        <div className="SearchOpponent">
            <div className="Opponent">
                <img src="https://clikiads.com/static/images/blank_profile.png" alt=""></img>
                <h2>Procurar oponente</h2>
            </div>
        </div>

        <button className="btn schOp" onClick={this.searchOpponent}>Procurar oponente</button>
        
         </>)
        } else {
            if(this.state.bundle !== null){
              return  (<Redirect to={"/match/"+ this.state.opponent.fb_id} />);
            } else {
            if(!this.state.chooseBundle){
            return(<>
            
                <CoinsTopItem user={this.state.user} />
                
                <div className="SearchOpponent">
                    <div className="Opponent">
                        <img src={picture(this.state.opponent.fb_id)} alt=""></img>
                        <h2>{this.state.opponent.firstname +" "+ this.state.opponent.lastname}</h2>
                        {this.state.OpponentStts}
                    </div>
                </div>
        
                <button className="btn schOp" onClick={this.bundles}>Escolher pacote</button>
                
                 </>)
            } else {
                return(<>
            
                    <CoinsTopItem user={this.state.user} />
                    
                    <div className="SearchOpponent">
                        <h2 style={{color: '#fff'}}>Escolha um pacote</h2>
                    <div className="media-list media-list-hover media-list-divided">
                    
                    

                    {
                        this.state.bundleList.map((bundle)=> 

                            <button onClick={ (e)=>{ this.setBundle(bundle.id)} } style={{width: '100%', borderRadius: '15px'}} className="media media-single">
                            <img className="w-80px" src={bundle.image} alt="..."></img>
                            <div className="media-body">
                        <h6>{bundle.title}</h6>
                                <small className="text-fader">{bundle.about}</small>
                            </div>
                            </button>


                        )
                    }

                    </div>
                    </div>
            
                    
                    
                     </>)
            }
        }
        }
    }

}