import React, {Component, useEffect} from 'react';
import Lottie from 'react-lottie';
import animationData from "./json/correct.json";
import wrongAnim from "./json/13865-sign-for-error-flat-style.json"
import "./assets/fonts/Mikado.css";
import FBLogin from 'react-facebook-login';
import axios from 'axios';
import {Redirect} from 'react-router-dom';
import picture from "./libs/picture";


export default class Match extends Component{
    constructor(props){
        super(props);
        this.state = {opponentPoints: 0, user: null, opponent: null, question: null, elapsedTime: 0, answered: false, myPoints: 0, answerTime: 10, startD: new Date(), status: null, gainedPoints: 0}
        this.getOpponent();
        this.getUser();
    }
    componentDidMount() {
        this.setState({startD: new Date()})
        
    }

    componentWillMount(){
        if(!this.state.answered){
            if(this.state.question === null){
                this.getQuestion()
            }
        }
    }

    componentDidUpdate() {
        if(this.state.answered){
            this.setState({startD: new Date()})
            this.setState({answered: false})
        }
        
    }

    startTime = ()=> {
        
            this.setState({startD: new Date()})

    }

    getOpponent = async () => {
        let params = new URLSearchParams();
        params.append('action', "singleUser");
        params.append('user', this.props.match.params.id);

      //  console.log(this.props.match.params.id)


        await axios("https://clickadsbr.com/api.php", {method: "post", data: params, withCredentials: true}).then((res)=> {
            console.log(res.data)

            this.setState({opponent: res.data})

         })

    }


    getUser = async () => {
        let params = new URLSearchParams();
        params.append('action', "userData");

      //  console.log(this.props.match.params.id)


        await axios("https://clickadsbr.com/api.php", {method: "post", data: params, withCredentials: true}).then((res)=> {
            console.log(res.data)

            this.setState({user: res.data})

         })

    }


    click = (e) => {
        var SEC = 0;
        if(!this.state.answered){
          var W =  setInterval(()=>{
               SEC++;
                if(SEC >= 10){
                  //  console.log("acabou " + SEC)
                    clearInterval(W)
                    //var firstTest = document.querySelector("Alternative:first-child").click();
                  //  firstTest.click();
                  //  console.log(firstTest)
                  if(e !== null){
                  e.click();
                  }
                }
               // console.log(SEC)
            }, 1000)
        }
    }

    answer = async (res) => {
        console.log(res.currentTarget.value)
        this.setState({gainedPoints: 0, elapsedTime: 0})
        var t1 = new Date();
        var t2 = this.state.startD;
        var dif = t1.getTime() - t2.getTime();
        console.log(t1)
        console.log(t2)
        var Seconds_from_T1_to_T2 = dif / 1000;
        var Seconds_Between_Dates = Math.abs(Seconds_from_T1_to_T2);
        this.setState({elapsedTime: Math.abs(Seconds_Between_Dates)})
        // alert(Seconds_Between_Dates)




        let params = new URLSearchParams();
        params.append('action', "answerQuestion");
        params.append('question', this.state.question.id);
        params.append("answer", res.currentTarget.value)
        params.append("answerTime", this.state.elapsedTime)
        params.append("sbd", Seconds_Between_Dates);


      //  console.log(this.props.match.params.id)


        await axios("https://clickadsbr.com/api.php", {method: "post", data: params, withCredentials: true}).then((v)=> {
          console.log(v.data)

            

            if(v.data.response === "correct"){
                this.setState({status: 'correct'})
                //   res.currentTarget.classList.add("correct");
                if(Math.floor(4000 * ((10 - Seconds_Between_Dates)/10)) > 0){
                    this.setState({myPoints: this.state.myPoints + Math.floor(4000 * ((10 - Seconds_Between_Dates)/10))})
                    this.setState({gainedPoints: Math.floor(4000 * ((10 - Seconds_Between_Dates)/10))})
                    }
            
            } else {
            //    res.currentTarget.classList.add("wrong");
                this.setState({status: 'wrong'})
            }

            
            
            
            //this.setState({status: 'correct'})
            this.setState({answered: true})
            setTimeout(()=> {
                this.setState({question: null})
                this.getQuestion();
                this.setState({status: null})
                
            }, 3000)



        })







        
    }


    getQuestion = async () => {

        let params = new URLSearchParams();
        params.append('action', "getQuestion");
        params.append('opponent', this.props.match.params.id);

      //  console.log(this.props.match.params.id)


        await axios("https://clickadsbr.com/api.php", {method: "post", data: params, withCredentials: true}).then((res)=> {
            console.log(res.data)

            this.setState({question: res.data})

         })
    }

    

    render(){
        //console.log(this.state.startD)
        
        if(this.state.status === null && this.state.question !== null && this.state.question.status !== "end" && this.state.opponent !== null && this.state.user !== null){
          

        
        return(
            <>
        <div ref={this.startTime} className="Players flexbox">
            <div className='versus'>
                vs
            </div>
            <div className="Player me">
                <div className="playerInfo">
            <span className="avatar avatar-xl status-warning"><img src={picture(this.state.user._fbid)} alt=""></img></span>
            <p className="me_name">{this.state.user.firstname}</p>

                </div>
            <h3 className="points">{this.state.myPoints}</h3>
            </div>
            <div className="Player enemy">
            <div className="playerInfo">
            <span className="avatar avatar-xl status-warning"><img src={picture(this.state.opponent.fb_id)} alt=""></img></span>
            <p className="me_name">{this.state.opponent.firstname}</p>

                </div>
        <h3 className="points">{this.state.opponentPoints}</h3>
            </div>
        </div>
        <div className="qv"></div>
        <div className="Question withImage" style={{background: "url("+ this.state.question.content + ")", backgroundPosition: 'center'}}>
            
            <h6>{this.state.question.title}</h6>
        </div>
        <div className="Alternatives">
            {
                this.state.question.alternatives.map((question, k) => {
                    if(k === 0){
                      return  <button value={question.id} data-r={k} ref={this.click} className="Alternative" onClick={(e) => this.answer(e)}>
                         <h6 className="Alternative">{question.title}</h6>
                        </button>
                        } else {
                      return     <button value={question.id} data-r={k} className="Alternative" onClick={(e) => this.answer(e)}>
                           <h6 className="Alternative">{question.title}</h6>
                          </button>
                        }
                } 
                
                )
            }

        </div>
        </>
        )
        } else {
            
            if(this.state.opponent === null){
                return(<></>)
            }
            if(this.state.user === null){
                return(<></>)
            }


            if(this.state.status === "correct"){
            const defaultOptions = {
                loop: false,
                autoplay: true, 
                animationData: animationData,
                rendererSettings: {
                  preserveAspectRatio: 'xMidYMid slice'
                }
              };

            return (<>
            <h1 style={{margin: '20px', textAlign: 'center', color: '#fff', fontWeight: 600}}>Você acertou!</h1>
            <Lottie options={defaultOptions} />
            <h4 style={{textAlign: 'center', color: '#fff'}}>Você ganhou</h4>
            <h2 style={{color: '#ffbb0d', textAlign: 'center', fontWeight: 800}}>{this.state.gainedPoints} pontos</h2>
            <h6 style={{textAlign: 'center',width: '60%',margin:'auto',color:'#fff'}}>Você respondeu em {this.state.elapsedTime.toFixed(1)}s</h6>
            </>)
        }


        if(this.state.status === "wrong"){
            const defaultOptions = {
                loop: false,
                autoplay: true, 
                animationData: wrongAnim,
                rendererSettings: {
                  preserveAspectRatio: 'xMidYMid slice'
                }
              };

            return (<>
            <h1 style={{margin: '20px', textAlign: 'center', color: '#fff', fontWeight: 600}}>Você errou!</h1>
            <Lottie options={defaultOptions} />
            <h4 style={{textAlign: 'center', color: '#fff'}}>Você não ganhou pontos.</h4>
            </>)
        }



        if(this.state.question === null){
            return(<></>)
        }

        if(this.state.question.status === "end" || this.state.question.id === null){
            return(<Redirect to="/browse" />)
        }
    }
    }
}