import React, {Component} from 'react';
import FBLogin from 'react-facebook-login/dist/facebook-login-render-props';
import axios from 'axios';
import { Redirect, useHistory } from 'react-router-dom';

export default class Login extends Component {
    constructor(props){
        super(props);
        this.state = {firstname: null, id: null, lastname: null, pic: null, email: null, link: null, isLogged: false}
       // this.checkLogin();
    }

    
    checkLogin = () => {
        alert('none')
    }
    login = async (e) => {
        console.log(e);
        let params = new URLSearchParams();
        params.append('action', "fbLogin");
        params.append('fb_id', e.userID);
        params.append('fb_email', e.email);
        params.append("fb_firstname", e.first_name);
        params.append("fb_lastname", e.last_name);

        await axios("https://clickadsbr.com/login.php", {method: "post", data: params, withCredentials: true}).then((res)=> {
            if(res.data.firstname.length > 0){
                //alert(res.data.firstname)
                console.log(res.data)
            this.setState({isLogged: true});

            console.log(this.state.isLogged)
            }
         })
           // console.log(res.data);
        
        
    }
    
    render(){
        if(this.state.isLogged){
            return(
                <>
                <h1>{this.state.isLogged}</h1>
                <Redirect to="/browse" />
                </>
                )


        
    } else {
        
        
        return(
            <> 
            <div className="loginContainer">
            <h1 style={{color: '#fff', marginBottom: '20px'}}> Entre no Quizzr e explore um novo mundo. </h1>
            <FBLogin
    appId="2035357803429277"
    
    fields="first_name,last_name,email,picture,id"
    version="3.1"
    scope="public_profile"
    callback={this.login}
    render={renderProps => (
        <button className="facebookLoginBtn" onClick={renderProps.onClick}><h5><i style={{padding: '5px 10px 5px 10px'}} className="fa fa-facebook"></i> Login com o Facebook</h5></button>
      )}
            />
            </div>
    
            </>
        )


    }
    }
}